@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap&subset=cyrillic);
body {
  width: 100%;
  background: #3d7777 no-repeat center center;
  background-size: cover;
  padding: 0;
  margin: 0;
  border: 0;
  font-family: 'Roboto', sans-serif;
  color: #FFF4F4; }

#app {
  min-width: 740px;
  min-height: 1080px;
  display: grid;
  grid-template: 125px 1fr 125px / 95px 1fr 95px; }

@charset "UTF-8";
#icons-bar {
  width: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer; }
  #icons-bar img {
    width: 30px;
    height: 30px; }

#city-info {
  height: 150px;
  margin-top: 50px; }
  #city-info #city-info-name {
    height: 80px;
    font-size: 62px; }
  #city-info #city-info-weather {
    height: 40px;
    font-size: 35px; }

#cd-weather-icon {
  width: 125px;
  height: 125px;
  margin-top: 35px; }

#cd-temp {
  height: 110px;
  margin-top: 40px;
  font-size: 100px;
  color: #92deb5; }

#сd-weather-values {
  height: 240px;
  margin-top: 45px; }
  #сd-weather-values div {
    height: 60px;
    font-size: 40px; }

.city-default {
  height: 100%; }

@keyframes lds-eclipse {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@-webkit-keyframes lds-eclipse {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.lds-eclipse {
  width: 100%;
  height: 100%;
  position: relative; }

.lds-eclipse div {
  position: absolute;
  -webkit-animation: lds-eclipse 1s linear infinite;
  animation: lds-eclipse 1s linear infinite;
  width: 160px;
  height: 160px;
  top: 20px;
  left: 20px;
  border-radius: 50%;
  box-shadow: 0 4px 0 0 #92deb5;
  -webkit-transform-origin: 80px 82px;
  transform-origin: 80px 82px; }

.lds-eclipse {
  width: 200px !important;
  height: 200px !important;
  -webkit-transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
  transform: translate(-100px, -100px) scale(1) translate(100px, 100px); }

.lds-css {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

#city-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap; }
  @media (min-width: 1250px) {
    #city-list {
      grid-area: 1 / 3 / span 1/ span 2; } }

.city-list {
  width: 100%;
  min-height: 722px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap; }

.city-list-item {
  width: 520px;
  height: 350px;
  background-color: #084a4a4d;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px; }

#tab-info {
  height: 30%;
  width: 98%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

#item-info {
  height: 70%;
  width: 100%; }

#tab-info-city {
  height: 100%;
  flex-grow: 60;
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: bold; }

#tab-info-icon {
  height: 100%;
  flex-grow: 20;
  display: flex;
  justify-content: space-around;
  align-items: center; }

#tab-info-delete {
  height: 100%;
  flex-grow: 13;
  display: flex;
  justify-content: space-around;
  align-items: center; }

#item-info {
  width: 98%;
  display: flex; }

#item-info-left {
  width: 30%;
  height: 100%; }

#item-info-right {
  width: 70%;
  height: 100%; }
  #item-info-right .weather-info-line {
    text-align: end; }

.weather-info-line {
  height: 60px;
  font-size: 25px;
  color: rgba(255, 244, 244, 0.75); }

.error-message {
  font-size: 28px; }

#tab-info-delete-svg {
  cursor: pointer; }

#input-box-main {
  height: 85px;
  width: 100%; }

#input-box {
  height: 45px;
  width: 520px;
  background: -webkit-gradient(linear, left bottom, left top, from(#92deb5), to(rgba(222, 146, 156, 0)));
  background: linear-gradient(0deg, #92deb5 0%, rgba(222, 146, 156, 0) 100%);
  border: 1px black; }

#input-form {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

#input-field {
  height: 45px;
  background-color: transparent;
  border-color: transparent;
  flex-grow: 1;
  font-size: 32px;
  color: #243a4e; }

*:focus {
  outline: none; }

#search-button {
  width: 100px;
  height: 45px;
  background: rgba(0, 0, 0, 0.7);
  border-color: transparent;
  color: #FFF4F4;
  font-size: 20px; }

#main-page {
  grid-area: 2 / 2 / span 1/ span 1;
  display: grid; }
  @media (min-width: 1250px) {
    #main-page {
      grid-template: 1fr / 525px 1fr 525px 525px; } }

